/* --- Sidebar Styles --- */
.sidebar {
  background: var(--bg-color-card);
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 1.5rem;
  position: sticky;
  top: 90px;
  height: calc(100vh - 90px - 70px);
  overflow-y: auto;
}
.sidebar .form-section { padding: 0; box-shadow: none; }
.sidebar .btn-primary { width: 100%; }

.sidebar-module-list { list-style-type: none; margin-top: 1rem; }
.sidebar-submodule-list {
  list-style-type: none; padding-left: 1rem;
  margin-top: 0.5rem; border-left: 2px solid #eee;
}
.sidebar-module-item { margin-bottom: 0.4rem; }
.sidebar-module-title {
  display: flex; justify-content: space-between; align-items: center;
  padding: 0.6rem 0.8rem;
  border-radius: 5px; background: var(--bg-color-alt);
  border: 1px solid #eee; transition: background-color 0.2s, border-color 0.2s;
  cursor: pointer; margin-bottom: 0.4rem;
}
.sidebar-module-title span {
  font-weight: 500;
  cursor: pointer; flex-grow: 1; word-break: break-all;
  font-size: 0.9rem;
}
.sidebar-module-title.active {
  background: var(--primary-color-light);
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}
.sidebar-module-title.active span {
  font-weight: 600;
}
.sidebar-module-title:hover:not(.active) { background: #f1f3f5; }
.sidebar-module-actions { display: flex; gap: 0.25rem; margin-left: 0.5rem; }
.btn-icon {
  background: #e9ecef; border: none; color: #495057;
  width: 22px; height: 22px;
  border-radius: 50%;
  font-size: 1rem;
  font-weight: 600; line-height: 21px;
  text-align: center; cursor: pointer; transition: all 0.2s;
}
.btn-icon:hover { background: #ced4da; }
.btn-remove-icon { background: var(--danger-color-light); color: #c0392b; font-size: 1.2rem; }
.btn-remove-icon:hover { background: var(--danger-color); color: white; }
hr.sidebar-divider { border: none; border-top: 1px solid #eee; margin: 1.25rem 0; }
.sidebar h3 {
  color: var(--text-color-muted);
  font-weight: 600;
  margin-bottom: 0.75rem;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}